const LocalStorage = {
  /**
   * localStorage
   */
  setStore: (name, content) => {
    if (!name) return;
    if (typeof content !== 'string') {
      content = JSON.stringify(content);
    }
    window.localStorage.setItem(name, content);
  },
  /**
   * localStorage
   */
  getStore: (name) => {
    if (!name) return;
    return window.localStorage.getItem(name);
  },
  /**
   * localStorage
   */
  removeStore: (name) => {
    if (!name) return;
    window.localStorage.removeItem(name);
  }
}


let hostname = window.location.hostname,
  BASE_URL = ''

const UrlConfig = {
  isuser: BASE_URL + '/hsll/check/isuser', // 
  register: BASE_URL + '/hsll/user/register', // 
  myorder: BASE_URL + '/hsll/myorder', // 
  takeGood: BASE_URL + '/hsll/take/good', // 
  getUserMessage: BASE_URL + '/hsll/getUserMessage', // 
  takeGood: BASE_URL + '/hsll/confirm/takeGood', // 
  inviteCode: BASE_URL + '/hsll/check/inviteCode', // 
  selAddress: BASE_URL + '/hsll/edit/address', // /

};

export default {
  LocalStorage,
  UrlConfig
};



