

import utils from '../../assets/js/urlConfig.js'
import Toast from 'vux/src/components/toast/index.vue'

export default {
  components: {
        Toast
    },
    data() {
        return {
          toastTxt: '',
          toastShow: false,
          imgShow:false,
          items:[
            {                //Object    
                "id":"123",                //String    id
                "person":"2",                //String    
                "phone":"18888888888",                //String    
                "address":"",                //String    
                "imgShow":false
            },
            {                //Object    
                "id":"123",                //String    id
                "person":"1",                //String    
                "phone":"18888888888",                //String    
                "address":"",                //String    
                  "imgShow":false
            }
          ],
        }
    },
    created(){
      document.title= ""
    },
    methods:{
      imgShowClick(item){
        console.log(item)
        for(var i in this.items){
          this.items[i].imgShow = false
        }
        item.imgShow = true
        let datas = {
          "openId":utils.LocalStorage.getStore('openId'),
          "id":item.address,
          "person":item.person,
          "phone":item.phone,
          "address":item.address

        }
        this.$http.post(utils.UrlConfig.selAddress,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){// 
                            this.toastShow = true
                            this.toastTxt = ""
                          }else{ // 
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }
                      }).catch(error => {
                          console.log(error)
                      })
      }
    }

}




